import React, { useState } from "react";
// import { Button } from "reactstrap";

// core components
import CSNavbar from "components/Navbars/Navbar.js";
import Footer from "components/Footer/Footer.js";

// meowfficer component
import MeowfficerAllTalentModal from "views/components/Meowfficer/MeowfficerAllTalentModal";
import MeowfficerList from "views/components/Meowfficer/MeowfficerList";
import MeowfficerModal from "views/components/Meowfficer/MeowfficerModal";

// json
import MeowfficerData from "assets/json/MeowfficerData.json";

// to do list
// create talent modal

function Meowfficer() {
  const [modal, setModal]           = useState(false);
  const [skillModal, setSkillModal] = useState(false);
  const [allTalent, setAllTalent]   = useState(false);

  const [modalData, setModalData]   = useState({
    name   : null,
    codex  : null,
  });

  const openModal = (cat) => {
    setModalData({
      name  : cat.name ,
      codex : cat.codex,
    });
    setModal(true);
    window.history.replaceState(null, null, 
      "#/meowfficer?open=" + cat.name.toLowerCase()
    );
  };

  const openSkillModal = () => {
    setSkillModal(true);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase()) + "=skill"
    );
  }

  const closeModal = () => {
    setModal(false);
    window.history.replaceState(null, null, 
      "#/meowfficer"
    );
  };

  const closeSkillModal = () => {
    setSkillModal(false);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase())
    );
  }

  const openAllTalent = () => {
    setAllTalent(true);
    window.history.replaceState(null, null, 
      "#/meowfficer?open=AllTalentList"
    );
  }

  const closeAllTalent = () => {
    setAllTalent(false);
    window.history.replaceState(null, null, 
      "#/meowfficer"
    );
  }

  React.useEffect(() => {
    try {
      const urlink = window.location.href.split("=");
      if (urlink.length > 1) {
        let cat = "";
        let codex = 0;

        for(var i = 0; i < MeowfficerData.length; i++) {
          if(MeowfficerData[i].name.toLowerCase() === urlink[1]) {
            cat   = MeowfficerData[i];
            codex = i;
          }
        }
        setModalData({
          name : cat.name,
          codex: codex,
        })
      setModal(true);
      if(urlink.length === 3) {setSkillModal(true)}
      }
    } catch (e) {
      console.log("Meowfficer not found.");
      window.history.replaceState(null, null, "#/meowfficer");
    }
  }, []);

  return (
    <>
      <CSNavbar />
      <div className="wrapper">
        <div className="page-header meowfficerheader">
          <div className="content">
            <h1>Meowfficer</h1>
            <MeowfficerAllTalentModal 
              openAllTalent ={openAllTalent}
              allTalentOpen ={allTalent}
              allTalentClose={closeAllTalent}
            />
            <div className="containerCS">
              {MeowfficerData.map((cat, key) => {
                const name = cat.name;
                const keys = "List" + key + name
                return (
                  <MeowfficerList
                    key       ={keys     }
                    codex     ={key      }
                    openModal ={openModal}
                    name      ={name     }
                  />
                );
              })}
            </div>
            <MeowfficerModal 
              modalData      ={modalData      }
              modalOpen      ={modal          }
              modalClose     ={closeModal     }
              openSkillModal ={openSkillModal }
              skillModalOpen ={skillModal     }
              skillModalClose={closeSkillModal}             
            />
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
}

export default Meowfficer;
